

































interface RuleFormValue {
  name: string;
  email: string;
  identity: string;
  sex: string;
  createAt: number;
}
import formMixin from "@/mixins/form";
import {
  Component,
  Prop,
  Watch,
  Emit,
  Vue,
  Mixins
} from "vue-property-decorator";
@Component({})
export default class AddDialog extends Mixins(formMixin) {
  ruleForm: RuleFormValue = {
    name: "",
    email: "",
    identity: "",
    sex: "",
    createAt: Date.now()
  };
  rules: any = {
    name: [
      { required: true, message: "请输入用户名", trigger: "blur" },
      { min: 3, max: 5, message: "长度在 3 到 8 个字符", trigger: "blur" }
    ],
    email: [{ required: true, message: "请输入邮箱", trigger: "blur" }],
    identity: [{ required: true, message: "请选择身份", trigger: "change" }],
    sex: [{ required: true, message: "请选择性别", trigger: "change" }]
  };

  resetForm(formName) {
    (this as any).$refs[formName].resetFields();
  }

  async _saveData() {
    let res = await (this as any).$refs.ruleForm.validate();
    let query = {
      ...this.ruleForm
    };
    if (res) {
      return this.saveData(
        this.$API.userModel.AddUser,
        query
      );
    }
  }
}
